<template>
  <div class="mng-add container">
      <van-nav-bar
        fixed
        left-text="预约会议"
        right-text="提交"
        @click-left="onClickLeft"
        @click-right="onClickRight"
        left-arrow>
      </van-nav-bar>
      <van-cell-group class="login-container-form">
        <!-- 主题 -->
        <van-field
          v-model="form.theme"
          label="主题"
          clearable>
        </van-field>
        <!-- 会议类型 -->
        <van-field
          v-model="form.type"
          label="会议类型"
          readonly
          right-icon="arrow"
          @click="selectType">
        </van-field>
        <!-- 组织者 -->
        <van-field
          v-model="form.organizer"
          label="组织者"
          readonly
          right-icon="arrow"
           @click="selectOrganizer">
        </van-field>
        <!-- 主办部门 -->
        <van-field
          v-model="form.dept"
          label="主办部门"
          readonly
          right-icon="arrow"
          @click="selectDept">
        </van-field>
        <!-- 会议室 -->
        <van-field
          v-model="form.room"
          label="会议室"
          readonly
          right-icon="arrow"
          @click="selectRoom">
        </van-field>
        <!-- 开始时间 -->
        <van-field
          v-model="form.start"
          label="开始时间"
          readonly
          right-icon="arrow"
          @click="selectStart">
        </van-field>
        <!-- 结束时间 -->
        <van-field
          v-model="form.end"
          label="结束时间"
          readonly
          right-icon="arrow"
          @click="selectEnd">
        </van-field>
        <!-- 内部参会人员 -->
        <van-field
          v-model="form.participant"
          label="内部参会人员"
          readonly
          right-icon="arrow"
           @click="selectParticipant">
        </van-field>

      </van-cell-group>
      <!-- 类型选择组件 -->
      <van-action-sheet
        v-model="showSheet"
        :actions="typeList"
        @select="selectFn"/>
      <!-- 时间选择组件 -->
      <van-popup
        v-model="showDate"
        round
        position="bottom"
        :style="{ height: '8rem' }"
      >
        <van-datetime-picker
          v-model="currentDate"
          type="datetime"
          @confirm="dateTimeConfirm"
          @cancel="dateTimeCancel"
        />
      </van-popup>
  </div>
</template>

<script>
import { NavBar, Field, CellGroup, ActionSheet, DatetimePicker, Popup } from 'vant'
import core from '@/mixins/core'
export default {
  components: {
    [NavBar.name]: NavBar,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [ActionSheet.name]: ActionSheet,
    [DatetimePicker.name]: DatetimePicker,
    [CellGroup.name]: CellGroup
  },
  mixins: [core],
  data () {
    return {
      form: {
        theme: '',
        type: '',
        organizer: '',
        dept: '',
        room: '',
        start: this.formatDate(new Date(), 'dateMin'),
        end: this.formatDate(new Date(), 'dateMin'),
        participant: ''
      },
      curDatePicker: 'start',
      showDate: false,
      showSheet: false,
      currentDate: new Date(),
      typeList: [
        { name: '选项1' },
        { name: '选项2' },
        { name: '选项3' }
      ]
    }
  },
  methods: {
    onClickLeft () {
      this.$router.push({
        path: '/manage/index',
        query: {}
      })
    },
    onClickRight () {

    },
    selectType () {
      this.showSheet = true
    },
    selectFn (item) {
      this.form.type = item.name
      this.showSheet = false
    },
    selectOrganizer () {

    },
    selectParticipant () {

    },
    selectDept () {

    },
    selectRoom () {

    },
    selectStart () {
      this.currentDate = new Date(this.form.start + ':00')
      this.curDatePicker = 'start'
      this.showDate = true
    },
    selectEnd () {
      this.currentDate = new Date(this.form.end + ':00')
      this.curDatePicker = 'end'
      this.showDate = true
    },
    dateTimeConfirm () {
      switch (this.curDatePicker) {
        case 'start':
          this.form.start = this.formatDate(this.currentDate, 'dateMin')
          break
        case 'end':
          this.form.end = this.formatDate(this.currentDate, 'dateMin')
          break
        default:
          break
      }
      this.showDate = false
    },
    dateTimeCancel () {
      this.showDate = false
    }
  }
}
</script>

<style scoped>

</style>



