export default {
  data () {
    return {

    }
  },
  created () {

  },
  methods: {
    formatDate (num, type) {
      const date = new Date(num)
      const yy = date.getFullYear()
      const dateM = String(date.getMonth() + 1)
      const mm = dateM.padStart(2, 0)
      const dateD = String(date.getDate())
      const dd = dateD.padStart(2, 0)
      const dateH = String(date.getHours())
      const HH = dateH.padStart(2, 0)
      const dateMM = String(date.getMinutes())
      const MM = dateMM.padStart(2, 0)
      const dateS = String(date.getSeconds())
      const SS = dateS.padStart(2, 0)
      let rst = ''
      switch (type) {
        case 'date':
          rst = `${yy}-${mm}-${dd}`
          break
        case 'dateMin':
          rst = `${yy}-${mm}-${dd} ${HH}:${MM}`
          break
        case 'dateTime':
          rst = `${yy}-${mm}-${dd} ${HH}:${MM}:${SS}`
          break
        default:
          break
      }
      return rst
    },
    isArray (val) {
      const tmp = Object.prototype.toString.call(val) === '[object Array]'
      return tmp
    },
    isString (val) {
      const tmp = Object.prototype.toString.call(val) === '[object String]'
      return tmp
    },
    isNumber (val) {
      const tmp = Object.prototype.toString.call(val) === '[object Number]'
      return tmp
    },
    isBoolean (val) {
      const tmp = Object.prototype.toString.call(val) === '[object Boolean]'
      return tmp
    },
    isUndefined (val) {
      const tmp = Object.prototype.toString.call(val) === '[object Undefined]'
      return tmp
    },
    isNull (val) {
      const tmp = Object.prototype.toString.call(val) === '[object Null]'
      return tmp
    },
    isObject (val) {
      const tmp = Object.prototype.toString.call(val) === '[object Object]'
      return tmp
    },
    isFunction (val) {
      const tmp = Object.prototype.toString.call(val) === '[object Function]'
      return tmp
    },
    timeShift (testTime) {
      const min = 60 * 1000
      const hour = 60 * min
      const day = 24 * hour
      const week = 7 * day
      const month = 30 * day
      const nowtime = Date.now()
      const timeStamp = new Date(testTime)
      const timeZone = nowtime - timeStamp
      let showTxt = ''
      if (timeZone < 0) {
        return testTime
      }
      if (timeZone > month) {
        showTxt = testTime
      } else if (timeZone > week) {
        showTxt = `${parseInt(timeZone / week)} 周前`
      } else if (timeZone > day) {
        showTxt = `${parseInt(timeZone / day)} 天前`
      } else if (timeZone > hour) {
        showTxt = `${parseInt(timeZone / hour)} 小时前`
      } else if (timeZone > min) {
        showTxt = `${parseInt(timeZone / min)} 分钟前`
      } else {
        showTxt = '刚刚'
      }
      return showTxt
    },
    debounce (foo, layms) {
      return function () {
        clearTimeout(this.debounce.id)
        const that = this
        const args = arguments
        this.debounce.id = setTimeout(() => {
          foo.apply(that, args)
        }, layms)
      }
    },
    deepClone (obj) {
      let tmpObj
      if (this.isObject(obj)) {
        tmpObj = {}
      } else if (this.isArray(obj)) {
        tmpObj = []
      } else {
        tmpObj = ''
      }
      for (const key in obj) {
        if (obj.hasOwnProperty(key)) {
          if (this.isObject(obj[key]) || this.isArray(obj[key])) {
            tmpObj[key] = this.deepClone(obj[key])
          } else {
            tmpObj[key] = obj[key]
          }
        }
      }
      return tmpObj
    }

  }
}



